<template>
    <div class="w-full px-[2%] sticky top-0 bg-white py-2.5 z-50 dark:bg-[#333] dark:border-b dark:border-b-[#3b3b3b] dark:text-[#d9d9d9]">
        <div class="max-w-7xl mx-auto flex justify-between items-center">
            <button @click="$emit('MenuIndex', 'section-0')">
                <img src="https://via.placeholder.com/120x50" alt="logo">
            </button>
            <div class="basis-3/4 hidden md:block">
                <ul class="flex justify-around">
                    <li class="relative" v-for="(e, index) in NaviList[0]" :key="e">
                        <button @click="$emit('MenuIndex', 'section-'+(index+1))">{{langList.Navi[index]}}</button>
                    </li>
                </ul>
            </div>
            <div class="basis-1/12 hidden md:block">
                <ul class="flex justify-between">
                    <li class="basis-2/4 text-center cursor-pointer"><font-awesome-icon :icon="dark ? 'sun' : 'moon'" class="text-2xl" @click="$emit('dark')"/></li>
                    <li class="basis-2/4 text-center cursor-pointer relative group">
                        <font-awesome-icon icon="globe" class="text-2xl"/>
                        <ul class="absolute -left-3 top-8 bg-white w-20 group-hover:border group-hover:h-20 transition-all rounded-md h-0 overflow-hidden">
                            <li @click="$emit('lang', 0); SelectLang(0)" class="pt-3 pb-0 hover:font-extrabold hover:text-lime-500"><button>한국어</button></li>
                            <li @click="$emit('lang', 1); SelectLang(1)" class="py-2.5 hover:font-extrabold hover:text-lime-500"><button>English</button></li>
                        </ul>
                    </li>
                </ul>
            </div>
            <div>
                <font-awesome-icon icon="bars" class="text-3xl cursor-pointer md:hidden" @click="isOpen = true"/>
            </div>
            <div :class="isOpen&& '!right-0'" class="w-72 h-full fixed darkMode bg-gray-100 z-50 p-12 box-border transition-all duration-500 top-0 -right-80 md:hidden">
                <font-awesome-icon icon="times" class="text-3xl cursor-pointer md:hidden absolute right-5 top-5" @click="isOpen=false"/>
                <div class="text-center mt-6">
                    <img src="https://via.placeholder.com/100" alt="image" class="mx-auto rounded-full mb-4">
                    <p>프론트엔드 개발자 조영민</p>
                    <ul class="mt-12">
                        <li v-for="(e, index) in NaviList[0]" :key="e" class="py-5 border-b">
                            <font-awesome-icon :icon="NaviList[2][index]" class="mr-2"/>
                            <button @click="$emit('MenuIndex', 'section-'+(index+1))">{{langList.Navi[index]}}</button>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div></div>
    <!-- 
    <font-awesome-icon icon="sun" />
    <font-awesome-icon icon="globe" />
    <font-awesome-icon icon="pencil" /> -->
</template>
<script>

export default {
    name: "NavPage",
    data() {
        return {
            isOpen: false,
            NaviList: [["프로필", "스킬", "포트폴리오"], ["/profile","/skill","/portfolio"],["user","code","folder-open"]]
            
        }
    },
    props:{
        dark: Boolean,
        langList: Object
    },
    methods:{
        SelectLang(index){
            localStorage.setItem("language", index);
        }
    }
}
</script>
