<template>
    <div class="fixed left-0 top-2/4 -translate-y-2/4 z-50">
        <div class="bg-white darkMode p-4 text-center absolute cursor-pointer mt-7 transition-all duration-500 left-0" :class="isOpen && 'left-36'">
            <font-awesome-icon icon="pencil" class="text-2xl" @click="isOpen === false ? isOpen = true : isOpen = false" />
            <ul class="absolute w-36 -left-36 top-0 bg-white darkMode duration-500 transition-all">
                <li v-for="(e, index) in FontList" :key="e" class="text-center py-2" :class="e.font" @mouseover="FontView(index)" @mouseout="FontRemove()"  @click="FontSelect(index)">{{ langList.FontTxt[index] }}</li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:'FontBox',
    props:{
      langList: Object
    },    
    data() {
        return {
            isOpen:false,
            fontData:'',
            FontList:[
                {
                    title:"부크크 명조",
                    font:"bookkmyungjo"
                },
                {
                    title:"이서윤체",
                    font:"leeseoyun"
                },
                {
                    title:"IM혜민체",
                    font:"imhyemin"
                },
                {
                    title:"조선굴림체",
                    font:"chosungulim"
                }
            ]
        }
    },
    methods: {
        FontSelect(index){
            document.querySelector("body").classList =[];
            document.querySelector("body").classList.add(this.FontList[index].font);
            localStorage.setItem("font", this.FontList[index].font)
            
        },
        FontView(index){
            document.querySelector("body").style.fontFamily = this.FontList[index].font;
        },
        FontRemove(){
            document.querySelector("body").style = [];
        }

    },
}
</script>
