<template>
  <FontBox :langList="langList[lang]"/>
  <!-- <NavPage @dark="DarkMode()"/> -->
  <NavPage @dark="toggleDark()" @MenuIndex="SectionMove($event)" :dark="isDark" @lang="lang=$event" :langList="langList[lang]"/>
  <div ref="section-0">
    <Home :langList="langList[lang]"/>
  </div>
  <div ref="section-1">
    <Profile />
  </div>
  <div ref="section-2">
    <Skill />
  </div>
  <div ref="section-3">
    <Portfolio />
  </div>
  <FooterPage />
</template>

<script>
import Home from './pages/Home.vue'
import Profile from './pages/Profile.vue'
import Skill from './pages/Skill.vue'
import Portfolio from './pages/Portfolio.vue'
import {useDark, useToggle} from '@vueuse/core'
import NavPage from './components/Navi.vue'
import FooterPage from './components/Footer.vue'
import FontBox from './components/FontBox.vue'
import Language from './assets/Language.json'
export default {
  name: 'App',
  data() {
    return {
      isDark: useDark(),
      toggleDark : useToggle(useDark()),
      fontStyle:"",
      MenuIndex: 'section-1',
      ArrayList: '',
      lang:0,
      langList: Language
    }
  },
  methods: {
    DarkMode(){
      document.querySelector("html").classList.toggle("dark")
    },
    SectionMove(index){
      this.MenuIndex = index;
      const rect = this.ArrayList[this.MenuIndex].getBoundingClientRect();
      const offset = rect.top + window.scrollY-50;
      window.scrollTo({
        top: offset,
        behavior: 'smooth'
      })
    }
  },
  components: {
    NavPage,
    FooterPage,
    FontBox,
    Home,
    Profile,
    Skill,
    Portfolio
  },
  mounted() {
    this.fontStyle = localStorage.getItem("font");
    this.lang = localStorage.getItem("language");
    document.querySelector("body").classList.add(this.fontStyle);
    this.ArrayList = this.$refs;
    
  },
}
</script>

